<template>
    <gray-mask
        :show="true"
    >
        <Dialog
            v-if="isApplying"
            :show.sync="show"
        >
            <div class="applying">
                <p>当前账号存在风险</p>
                <p>申诉中</p>
            </div>
        </Dialog>
        <Dialog
            v-else
            :show.sync="show"
            btnText="我要申诉"
            :clickHandler="clickHander"
        >
            <div
                slot="msg"
                class="msg">
                <p>当前账号存在风险</p>
                <p>无法提现</p>
            </div>
        </Dialog>
    </gray-mask>
</template>

<script>
import GrayMask from '@/components/GrayMask';
import Dialog from '@/components/Dialog';
import { openCommonWebViewByPath } from '@/utils/jsApiV2/client';

export default {
    components: {
        GrayMask,
        Dialog,
    },
    props: {
        isApplying: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            show: true,
        };
    },
    watch: {
        show(v) {
            if (!v) {
                this.$emit('close');
            }
        },
    },
    methods: {
        clickHander() {
            this.$emit('close', true);
            this.$nextTick(() => {
                openCommonWebViewByPath('/mobile-v2/withdraw/apply', '', false, '');
            });
        },
    },
};
</script>

<style scoped>
.dialog >>> button {
    background-color: rgba(255, 67, 71, 1);
    margin-bottom: 34px;
}

.msg,
.applying {
    margin: 41px 22px 48px;
    text-align: center;
    font: 18px/30px miui;
    color: rgba(55, 55, 55, 1);
}

.applying {
    margin: 44px 50px;
}
</style>
