import request from './request';

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=132820828
 * 通用参数 && 用户登录相关 cookie
 */
export function getInterest(data = {}) {
    return request.get('/api/v4/interest/list', data);
}

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=132820828
 * 通用参数 && { categories: '["历史","文化"]' } && 用户登录相关 cookie
 */
export function postInterest(data = {}) {
    return request.get('/api/v4/interest/select', data);
}

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=282839148
 * 账号异常状态查询
 */
export function getUserStatus(data = {}) {
    return request.get('/api/v1/user/forbidden-status', data);
}
