<template>
    <div class="fallback">
        <div
            class="Hairline Hairline_right-outer"
            @click="fallback('bikan://goto/feedback')"
        >我要反馈</div>
        <div
            :class="{
                unread: hasUnreadMessage,
            }"
            @click="fallback('bikan://goto/feedbackHistory')"
        >反馈记录</div>
    </div>
</template>

<script>
import { getUserAccountInfo, doLogin } from '@/utils/jsApiV2/client';
import { get } from '@/api/fallback';


export default {
    name: 'QuestionFallback',
    data() {
        return {
            hasUnreadMessage: false,
        };
    },
    created() {
        get().then((res) => {
            this.hasUnreadMessage = res.data > 0;
        });
    },
    methods: {
        fallback(deeplink) {
            let userAccountInfo = getUserAccountInfo() || {};

            if (userAccountInfo.user_id) {
                this.go(deeplink);
            } else {
                window.$QuestionFallbackCallback = () => {
                    userAccountInfo = getUserAccountInfo() || {};

                    // 登录成功
                    if (userAccountInfo.user_id) {
                        this.go(deeplink);
                    }
                };
                doLogin('$QuestionFallbackCallback');
            }
        },
        go(deeplink) {
            location.href = deeplink;
        },
    },
};
</script>

<style scoped>
.fallback {
    position: fixed;
    bottom: 0;
    z-index: 999;
    background-color: #fff;
    font-size: 16px;
    line-height: 16px;
    height: 54px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 23px 0 rgba(0, 0, 0, 0.1);
}

.fallback div {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
}

.fallback div::before {
    content: "";
    display: block;
    height: 17px;
    width: 17px;
    background-size: 17px 17px;
    margin-right: 4px;
}

.fallback div:first-child::before {
    background-image: url(./image/fallback-write.png);
}

.fallback div:last-child::before {
    background-image: url(./image/fallback-recode.png);
}

.fallback div:last-child::after {
    content: "";
    display: block;
    height: 7px;
    width: 7px;
    border-radius: 100%;
    background-color: transparent;
    margin-top: -24px;
}

.unread::after {
    background-color: rgba(235, 73, 64, 1) !important;
}
</style>
