<template>
    <div class="container">
        <div
            :class="{
                content: true,
                'show-fallback': isShowFallback(),
            }"
            ref="content">
            <guide
                ref="guide"
                :types="types"
                @toTag="movePannel"/>
            <div
                class="pannel-list"
                ref="pannel">
                <pannel
                    v-for="(v, i) in types"
                    :header="v"
                    @goPath="goPath"
                    :appeal-status="appealStatus"
                    :info="content[v]"
                    :key="i"/>
            </div>
            <Fallback v-if="isShowFallback()" />
            <WithdrawApplyEntrance
                v-if="showWithdrawApplyDialog"
                :is-applying="appealStatus === '1'"
                @close="closeWithdrawApplyDialog"
            />
        </div>
    </div>
</template>

<script>
import config from '@/config';
import { getUserStatus } from '@/api/user';
import { getUserAccountInfo, openCommonWebViewByPath } from '@/utils/jsApiV2/client';
import WithdrawApplyEntrance from '../withdraw/WithdrawApplyEntrance';
import Guide from './guide';
import Pannel from './pannel';
import Fallback from './fallback';
import { getConfigByKey } from '../../api/common';

export default {
    name: 'QuestionCenter',
    components: {
        Guide,
        Pannel,
        Fallback,
        WithdrawApplyEntrance,
    },
    data() {
        return {
            title: '',
            types: [],
            content: {},
            appealStatus: '',
            inForbiddenStatus: false,
            showWithdrawApplyDialog: false,
        };
    },
    methods: {
        movePannel(i) {
            window.scrollTo(0, this.$refs.pannel.children[i].offsetTop
                - this.$refs.guide.$el.offsetHeight);
        },
        isShowFallback() {
            const version = Number(config.versionCode);

            if (version && version >= 40710) {
                return true;
            }

            return false;
        },
        closeWithdrawApplyDialog() {
            this.showWithdrawApplyDialog = false;
        },
        goPath(path) {
            this.getUserStatus().then(() => {
                if (this.appealStatus === '1') {
                    this.showWithdrawApplyDialog = true;
                } else {
                    openCommonWebViewByPath(path);
                }
            });
        },
        getUserStatus() {
            if (config.versionCode < 40890) {
                return Promise.resolve();
            }
            return getUserStatus({ userId: getUserAccountInfo().user_id }).then((res) => {
                this.appealStatus = res.data.data.appealStatus;
                this.inForbiddenStatus = res.data.data.inForbiddenStatus;
            });
        },
        getHelpText() {
            return getConfigByKey({
                key: 'fe-config_helper',
            }).then((res) => {
                this.title = res.data.title;
                this.types = res.data.types;
                this.content = res.data.content;
                this.$root.hidePlaceholder();
            }).catch(() => {
                this.$error(this.$route.name);
            });
        },
    },
    created() {
        Promise.all([this.getUserStatus(), this.getHelpText()]).then(() => {
            if (this.inForbiddenStatus) {
                this.content['账号/认证'].push({
                    title: '提示账号异常，无法领取金币',
                    des: [
                        { data: '想看希望您能一个手机固定一个账号，多账号登录的会被系统记录，会有封号风险！' },
                        { data: '点击申诉', type: 'button', path: '/mobile-v2/withdraw/apply' },
                    ],
                    tag: '申诉',
                });
            }
        });
    },
    updated() {
        this.$refs.content.style['padding-top'] = `${this.$refs.guide.$el.offsetHeight}px`;
    },
};
</script>

<style scoped>
.content {
    overflow: hidden;
    margin-top: 1px;
    min-height: 100vh;
}

.show-fallback {
    margin-bottom: 56px;
}

.pannel-list {
    overflow: scroll;
    min-height: 100vh;
}
</style>
