<template>
    <div class="container">
        <div
            class="header"
            :id="`item-${header}`">
            {{ header }}
        </div>
        <div
            class="content"
            v-for="(v, i) in data"
            :key="header + i"
            @click="toggle(i)">
            <div class="title">
                {{ v.title }}
                <div
                    v-if="v.tag"
                    class="title-tag">
                    {{ v.tag }}
                </div>
                <div
                    v-if="!v.expand"
                    :class="v.show ? 'arrow arrow_upturn' : 'arrow'" >
                    <div class="arrow_big"/>
                    <div class="arrow_small"/>
                </div>
                <div
                    class="des-box"
                    v-show="(v.show && !v.expand) || v.expand">
                    <p
                        :class="'des' + (item.type ? '_' + item.type : '')
                        + (item.class ? ' '+ item.class : '')"
                        v-for="(item,i) in v.des"
                        :key="v.title + i">

                        <template v-if="item.type === 'img'" >
                            <div :class="'imgbox ' ">
                                <img :src="item.data">
                            </div>
                        </template>
                        <template v-else-if="item.type === 'button'" >
                            <Button
                                @click.native.stop="goPath(item.path)"
                                class="appeal">
                                {{ item.data }}
                            </Button>
                        </template>
                        <template v-else>
                            {{ item.data }}
                        </template>
                    </p>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import Button from '@/components/Button';

export default {
    name: 'QuestionPannel',
    props: {
        header: {
            type: String,
            default: '',
        },
        info: {
            type: Array,
            default() {
                return [];
            },
        },
    },
    data() {
        return {
            data: [],
        };
    },
    methods: {
        toggle(i) {
            this.$set(this.data[i], 'show', !this.data[i].show);
        },
        goPath(path) {
            this.$emit('goPath', path);
        },
    },
    created() {
        this.data = this.info;
    },
    components: {
        Button,
    },
};
</script>

